
import {KDLApi} from '../../utils/KDLApi';
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {setUser,setAccessToken} from '../../utils/auth';
import {getUser} from '@/utils/auth';
import $ from 'jquery';
export default {
components: {
XHeader,
XInput,
Group,
XTextarea,
Toast
},
data () {
return {
name:'',
tel:'',
address:'',
showPositionValue: false,
text:''
}
},
mounted(){
var that = this;
KDLApi.MemberAddress.GetMemberAddressInfo().then(re=>{
if(re.data == null){

}else{
console.log(re.data)
that.name=re.data.MemberName,
that.tel=re.data.MemberPhoneNum,
that.address=re.data.MemberAddress
}
},err=>{

});
},
methods:{
setadd(){
var that = this;
if(that.name == ''||that.tel==''||that.address ==''){
that.showPositionValue = true
that.text = "请填写完整信息"
return false;
}

KDLApi.MemberAddress.SetMemberAddressInfo(
{
MemberName:that.name,
MemberAddress:that.address,
MemberPhoneNum:that.tel
}
).then(re=>{
console.log(re);
that.$router.push('/user/address');
},err=>{

});
}
}
}




