
import {KDLApi} from '../../utils/KDLApi';
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {setUser,setAccessToken} from '../../utils/auth';
import {getUser} from '@/utils/auth';
import $ from 'jquery';
export default {
components: {
XHeader,
Group,
Tab,
TabItem,
Toast
},
data () {
return {
filezheng:'',
fhlist:[],
showPrise:'',
resslist:[],
imgId:'',
T:'http://iasstest.bychine.com/r/',
kdipt:'',
tag: false,
showPositionValue: false,
text:'',
MemberPhoneNum:''
}
},
mounted(){
var that = this;
KDLApi.SendProduct.GetSendProductInfo(that.$route.query.SendId).then(re=>{   //获取当前会员相关发货信息
console.log(re.data);
re.data.LastSendTime = that.timestampToTime(re.data.LastSendTime);
re.data.ApplyTime = that.timestampToTime(re.data.ApplyTime);
that.fhlist  =  re.data
that.MemberPhoneNum=that.fhlist.ApplyMemberPhoneNum.replace(that.fhlist.ApplyMemberPhoneNum.substr(3,5),"****")
},err=>{

});





$("#getzImg").on("change", function(e){
console.log(e);
var file = e.target.files
that.filezheng=file
var uploadFile = new FormData($("#files")[0])

var _token = localStorage.getItem("token");

$.ajax({
type: "POST",
url: "/api/Media/Upload",
dataType : "text",
processData: false,
contentType: false,
data: uploadFile,
beforeSend: function(xhr) {
xhr.setRequestHeader("Authorization", "Bearer "+_token);
},
success: function(data) {

that.imgId = data
$(".zheng").css('display','block');
that.tag = true;
},
error:function(e){


}


});


});
},
methods:{
onItemClick(index){
console.log(index);
},
setbtn(SendId){
var that = this;
if(that.tag == false){
that.showPositionValue = true
that.text = "请上传快递单"
return false;
}else if(that.kdipt ==''){
that.showPositionValue = true
that.text = "请填写快递单号"
return false;
}
KDLApi.SendProduct.Send({SendId:SendId,LogisticsNum:that.kdipt,LogisticsImgId:that.imgId}).then(re=>{
console.log(re);
if(re){
that.showPositionValue = true
that.text = "操作成功"
setTimeout(function(){
that.$router.push('/user/Delivergoods');
},2000)
}
},err=>{

});
},
deleteimg(){
var that = this;
$(".zheng").css('display','none');
that.tag = false;
},
changeimg(){
var that = this;
setTimeout(function(){
$(".zheng").css('display','block');
that.tag = true;
},2000)

}
}
}




