import { KDLApi } from "../../utils/KDLApi";

import $ from "jquery";
export default {
  data() {
    return {
      SProduct: [], //收货列表
      FProduct: [] //收货列表
    };
  },

  created() {
    //发货列表
    KDLApi.SendProduct.QuerySendProductInfo({
      Status: "待发货",
      QueryMode: "收货人发货单"
    }).then(
      re => {
        this.FProduct = re.data.Items;
        console.log(this.FProduct);
      },
      err => {}
    );
    // 发货列表
    KDLApi.SendProduct.QuerySendProductInfo({
      Status: "待收货",
      QueryMode: "发货人发货单"
    }).then(
      re => {
        this.SProduct = re.data.Items;
      },
      err => {}
    );
  },
  methods: {
    //根据状态显示不同的样式
    getClassName(val) {
      if (val == "待收货") {
        return "d-status";
      } else if (val == "提醒发货") {
        return "t-status";
      }
    }
  },

  updated() {}
};



