import {setAccessToken as SISetAccessToken} from './service-invoker';

var _user={};
export function getUser(){
    return _user;
}
export function setUser(user){
    _user=user || {};
}
 
var _token=null;
export function setAccessToken(token){
    _token=token;
    SISetAccessToken(token)
}

export function isSignined(){
    return _token && _user;
}


