

import {KDLApi} from '../../utils/KDLApi';
import Toast from 'vux/src/components/toast/index.vue'
import InlineXNumber from 'vux/src/components/inline-x-number/index.vue'

import $ from 'jquery';
import { setTimeout } from 'timers';

export default {

data () {

return {

SelectVal:"",
product:[],
ApplyNum:"",//可申请量
Unit:"",
num:0,
num1:0,
address:[],//获取当前会员地址信息
MemberAddress:"",
MemberName:"",
MemberPhoneNum:"",
showPositionValue: false, //是否显示提示
position: 'center', //提示信息的位置
toastText: '', // 提示文本
fillable:true,
type:"",
isDisble:false,
isActive:false
}
},
components: {
Toast,InlineXNumber
},
created(){
var  that = this;
KDLApi.StockApply.QueryCanApplyProducts().then(re=>{ //可申请量
that.product=re.data;
that.SelectVal= that.product[0].ProductId





},err=>{

});




KDLApi.MemberAddress.GetMemberAddressInfo().then(re=>{
this.address=re.data
this.MemberAddress=this.address.MemberAddress
this.MemberName=this.address.MemberName
this.MemberPhoneNum=this.address.MemberPhoneNum.replace(this.address.MemberPhoneNum.substr(3,5),"****")

},err=>{

});
},
methods: {


Allapply(){
var that = this;
KDLApi.StockApply.QueryCanApplyProducts().then(re=>{ //可申请量
for(var i=0;i<re.data.length;i++){
console.log(re.data[i].ProductId)
if(this.SelectVal==re.data[i].ProductId){
that.num  = re.data[i].ApplyNum
}
}
},err=>{

});

} ,

Apply_Submit(){

var that = this;
KDLApi.StockApply.QueryCanApplyProducts().then(re=>{ //可申请量
for(var i=0;i<re.data.length;i++){

if(that.SelectVal==re.data[i].ProductId){
that.num1  = re.data[i].ApplyNum
if(!re.data[i].ApplyNum){
this.type="text"
this.showPositionValue = true;
this.toastText = "申请量不能大于可申请量";
return false;
}

var reg = /^[0-9]+.?[0-9]*$/;
if(this.num>this.num1){
this.showPositionValue = true;
this.type="text"
this.toastText = "申请量不能大于可申请量";
return
}else if(!reg.test(this.num)||this.num==0){
this.showPositionValue = true;
this.type="text"
this.toastText = "请填写申请量";
return
}
if(this.address==null){
this.showPositionValue = true;
this.type="text"
this.toastText = "请添加收货信息";
this.isActive=true
return
}
this.isDisble=true
KDLApi.StockApply.AddStockApply({
ProductId:this.SelectVal,
ApplyNum:this.num
}).then(re=>{ //发布供货申请
console.log(re);
if(re){
var that=this

this.showPositionValue = true;
this.type="success"
this.toastText = "提交成功";
setTimeout(function(){
that.$router.push('/home/kcapply')

},2000)
}

},err=>{

});
setTimeout(function(){
this.isDisble=false
},2000)
}
}
},err=>{

});
}
},


updated(){

}
}



