
import {KDLApi} from '../../utils/KDLApi';
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {setUser,setAccessToken} from '../../utils/auth';
import {getUser} from '@/utils/auth';
import $ from 'jquery';
export default {
components: {
XHeader,
Group,
Tab,
TabItem,
Toast
},
data () {
return {
fhlist:[],
T:'http://iasstest.bychine.com/r/',
showPositionValue: false,
text:''
}
},
mounted(){
var that = this;
KDLApi.SendProduct.GetSendProductInfo(that.$route.query.SendId).then(re=>{   //获取当前会员相关发货信息
console.log(re.data);
re.data.RealSendTime = that.timestampToTime(re.data.RealSendTime);
re.data.ApplyTime = that.timestampToTime(re.data.ApplyTime);
that.fhlist  =  re.data
},err=>{

});
},
methods:{
onItemClick(index){
console.log(index);
},
confirmbtn(SendId){
var that = this;
KDLApi.SendProduct.Recv(SendId).then(re=>{
console.log(re);
if(re){
that.showPositionValue = true
that.text = "操作成功"
setTimeout(function(){
that.$router.push('/user/Collectgoods');
},2000)
}
},err=>{

});
},
copyText(obj){
var that = this;
try{
const range = document.createRange();
range.selectNode(document.getElementById('idtsc'));
const selection = window.getSelection();
if (selection.rangeCount > 0) selection.removeAllRanges();
selection.addRange(range);
document.execCommand('copy');
that.showPositionValue = true
that.text = "复制成功"
}catch(e){
alert("您的浏览器不支持此复制功能，请选中相应内容并使用Ctrl+C进行复制!");
}
}
}
}




