
var _invoker=null;
function buildArguments(args,params){
	if(!params)return args[0];
	var re={};
	var c=Math.min(args.length,params.length);
	for(var i=0;i<c;i++)
		re[params[i]]=args[i];
	return re;
}
function buildFunc(svc,method,postArg,params){
	return function(){
		var args=buildArguments(arguments,params);
		return _invoker(svc,method,postArg,args);
	}
}

function build(svcs){
var re={};
for(var sn in svcs){
	var svc=svcs[sn];
	var sd=re[sn]={};
	for(var mn in svc){
		var m=svc[mn];
		sd[mn]=buildFunc(sn,mn,m[0],m[1]);
	}
}
return re;
}

export function setServiceInvoker(invoker){
	_invoker=invoker;
}

