
import Loading from 'vux/src/components/loading/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {KDLApi} from '../../utils/KDLApi'
import {setUser,setAccessToken} from '../../utils/auth'
import $ from 'jquery'
function parseFragments(){
var href=window.location.href;
var i=href.indexOf('#');
if(i==-1)return {};
var t=href.indexOf('?',i+1);
var str=href.substring((t==-1?i:t)+1);
var re={};
str.split('&').forEach(v=>{
var p=v.split('=');
re[decodeURIComponent(p[0])]=decodeURIComponent(p[1]);
});
return re;
}
var fragments=parseFragments();
export default {
components: {
XInput,
XButton,
Group,
Cell,
Toast
},
data () {
return {
code: '',
loadingText:'loading...',
showPositionValue: false,
text:'',
}
},
methods: {
signin () {
var that = this;
if(that.code == ''){
that.showPositionValue = true
that.text = "请输入账号"
return false;
}
this.$vux.loading.show({
text: '正在登录...'
})

KDLApi.ClientExtAuth.GetAuthArgument("test","app.ios").then(re=>{

console.log(re);

setTimeout(()=>{


KDLApi.ClientExtAuth.AuthCallback({
State:re.data.State,  //state来自GetAuthArgument
Arguments:{
code:this.code         
}
}).then(re=>{




setUser(re.data.User);
setAccessToken(re.data.AccessToken);
localStorage.setItem('token', re.data.AccessToken); 

this.$vux.loading.hide()
console.log(this.$route);

this.$router.replace({
path:this.$route.query.jump || "/",
query:this.$route.query.query?JSON.parse(this.$route.query.query):null
});
});

},1000);
});
},
signin_mp () {

var callback=window.urlbase+"#/user/signin-callback?jump="+encodeURIComponent(this.$route.query.jump)+"&query="+encodeURIComponent(this.$route.query.query);
location.replace(
"/api/pageextauth/start?provider=weixin.mp&clientid=browser.wx&state="+this.code+"&callback="+encodeURIComponent(callback)
);

},
onBlur(){

},


},
mounted(){
















}
}



