import Vue from 'vue'
import Router from 'vue-router'
import {isSignined} from '@/utils/auth'

//首页(底部TAB)
import HomeFrame from '@/components/Home/Frame.vue'
//首页
import KCHome from '@/components/KCHome/KCHome'
//提供
import KCprovide from '@/components/Provide/Home'
//申请
import KCapply from '@/components/Apply/ApplyHome'
//提供列表
import ProvideList from '@/components/Provide/ProvideList'
//申请列表
import ApplyList from '@/components/Apply/ApplyList'

//我的（用户相关）
import UserHome from '@/components/User/Home'
import UserSignin from '@/components/User/Signin'
import UserNameEdit from '@/components/User/NameEdit'
import UserSigninCallback from '@/components/User/SigninCallback'

// 地址管理
import Address from '@/components/User/Address'
import SetAddress from '@/components/User/SetAddress'
// 收货管理 
import Collectgoods from '@/components/User/Collectgoods'

// 发货管理  
import Delivergoods from '@/components/User/Delivergoods'
// 帮助中心
import Helpcenter from '@/components/User/Helpcenter'

// 收货详情相关
import SHpendingdelivery from '@/components/Docs/SHpendingdelivery'  //待发货详情
import SHwaitGoods from '@/components/Docs/SHwaitGoods'   //待收货详情
import SHReceivedGoods from '@/components/Docs/SHReceivedGoods'   //已收货详情


// 发货详情相关   
import FHpendingdelivery from '@/components/Docs/FHpendingdelivery'  //待发货详情
//
import FHwaitGoods from '@/components/Docs/FHwaitGoods'  //待收货详情
import FHReceivedGoods from '@/components/Docs/FHReceivedGoods'  //已收货详情

import { hackHashHistory } from './HackedHashHistory' 
Vue.use(Router)

var router= new Router({
  routes: [
    //默认页面
    { path: '', redirect: '/home/kchome',props:{public:true}},
    //H5首页
    {
      //H5首页 底部导航
      path: '/home', component: HomeFrame,
      
      children:[
        //H5首页  提供
        {path:'kcprovide', name: '提供', component: KCprovide},

        //H5首页
        {path:'kchome',name: '首页', component: KCHome},

        //H5首页 申请
        // {path:'caring',name: '关爱圈', component: CaringHome},
        {path:'kcapply',name: '申请', component: KCapply},
        
        //H5首页 我的
          {path:'user',name: '我的', component: UserHome},
          
        // 提供列表
        {path:'ProvideList', name: '提供列表', component: ProvideList},
         // 申请列表 
         {path:'ApplyList', name: '申请列表', component: ApplyList},
        // 地址管理
         {path:'/user/address', name: '地址管理',component:Address,props:{public:true}},
         {path:'/user/SetAddress',name: '地址管理',component:SetAddress,props:{public:true}},
         //收货管理 
         {path:'/user/Collectgoods',name: '收货管理',component:Collectgoods,props:{public:true}},
        //  发货管理  
        {path:'/user/Delivergoods',name: '发货管理',component:Delivergoods,props:{public:true}},
        // 帮助中心  
        {path:'/user/Helpcenter',name: '帮助中心',component:Helpcenter,props:{public:true}},
        //收货管理（待收货）
        {path:'/docs/SHpendingdelivery',component:SHpendingdelivery,props:{public:true}},
         {path:'/docs/SHwaitGoods',component:SHwaitGoods,props:{public:true}},
         {path:'/docs/SHReceivedGoods',component:SHReceivedGoods,props:{public:true}},
         //发货管理  
         {path:'/docs/FHpendingdelivery',component:FHpendingdelivery},
         {path:'/docs/FHwaitGoods',component:FHwaitGoods,props:{public:true}},
         {path:'/docs/FHReceivedGoods',component:FHReceivedGoods,props:{public:true}},
        ]
        
    },
    //------------------------------------------------------------------------
    //用户首页(APP/H5使用)
    {path:'/user',component:UserHome},
    {path:'/user/name-edit',component:UserNameEdit},
    //登录
    {path:'/user/signin',component:UserSignin,props:{public:true}},
    {path:'/user/signin-callback',component:UserSigninCallback,props:{public:true}},
   
    
    //------------------------------------------------------------------------ 
    //首页
    {path: '/kchome',component:KCHome,props:{public:true}}, 
    //------------------------------------------------------------------------
    //提供首页
    {path: '/kcprovide',component:KCprovide,props:{public:true}},
    //申请首页
    {path: '/kcapply',component:KCapply,props:{public:true}},
 //------------------------------------------------------------------------

  ]
});
hackHashHistory(router);
router.beforeEach((to,from,next)=>{
  var t=to.matched;
  //检查是否是公开页面
  if(t && (t=t[t.length-1]) && (t=t.props) && (t=t.default) && t.public)
  {
    next();
    return;
  }
  //检查是否已登录
  if(isSignined())
  {
    next();
    return;
  }
  
  next({ path: '/user/signin', query: { jump: to.path,query:JSON.stringify(to.query) }})
  //
  
});
export default router;


