
import {KDLApi} from '../../utils/KDLApi';
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import {setUser,setAccessToken} from '../../utils/auth';
import {getUser} from '@/utils/auth';
import $ from 'jquery';
export default {
components: {
XHeader,
Group,
Tab,
TabItem
},
data () {
return {
colllist:[]
}
},
mounted(){
var that = this;
KDLApi.SendProduct.QuerySendProductInfo({Status:'',QueryMode:'发货人发货单',Paging:{Count:100}}).then(re=>{   //获取当前会员相关发货信息

for(var i=0; i<re.data.Items.length;i++){
re.data.Items[i].LastSendTime = that.timestampToTime(re.data.Items[i].LastSendTime);
re.data.Items[i].RealSendTime = that.timestampToTime(re.data.Items[i].RealSendTime);
}

that.colllist = re.data.Items;
},err=>{

});
},
methods:{
onItemClick(index){

if(index==0){
var that = this;
KDLApi.SendProduct.QuerySendProductInfo({Status:'',QueryMode:'发货人发货单',Paging:{Count:100}}).then(re=>{   //获取当前会员相关发货信息

for(var i=0; i<re.data.Items.length;i++){
re.data.Items[i].LastSendTime = that.timestampToTime(re.data.Items[i].LastSendTime);
re.data.Items[i].RealSendTime = that.timestampToTime(re.data.Items[i].RealSendTime);
}

that.colllist = re.data.Items;
},err=>{

});
}else if(index == 1){
var that = this;
KDLApi.SendProduct.QuerySendProductInfo({Status:'待发货',QueryMode:'发货人发货单',Paging:{Count:100}}).then(re=>{   //获取当前会员相关发货信息
for(var i=0; i<re.data.Items.length;i++){
re.data.Items[i].LastSendTime = that.timestampToTime(re.data.Items[i].LastSendTime);
re.data.Items[i].RealSendTime = that.timestampToTime(re.data.Items[i].RealSendTime);
}

that.colllist = re.data.Items;
},err=>{

});
} else if(index == 2){
var that = this;
KDLApi.SendProduct.QuerySendProductInfo({Status:'待收货',QueryMode:'发货人发货单',Paging:{Count:100}}).then(re=>{   //获取当前会员相关发货信息
for(var i=0; i<re.data.Items.length;i++){
re.data.Items[i].LastSendTime = that.timestampToTime(re.data.Items[i].LastSendTime);
re.data.Items[i].RealSendTime = that.timestampToTime(re.data.Items[i].RealSendTime);
}

that.colllist = re.data.Items;
},err=>{

});
}else{
var that = this;
KDLApi.SendProduct.QuerySendProductInfo({Status:'已完成',QueryMode:'发货人发货单',Paging:{Count:100}}).then(re=>{   //获取当前会员相关发货信息
for(var i=0; i<re.data.Items.length;i++){
re.data.Items[i].LastSendTime = that.timestampToTime(re.data.Items[i].LastSendTime);
re.data.Items[i].RealSendTime = that.timestampToTime(re.data.Items[i].RealSendTime);
}

that.colllist = re.data.Items;
},err=>{

});
}
},
fhlistbtn(item){
if(item.Status == '待发货'){    ///docs/FHReceivedGoods
this.$router.push({
path:'/docs/FHpendingdelivery',
query:{
SendId:item.SendId
}
})
}else if(item.Status == '已完成'){    //
this.$router.push({
path:'/docs/FHReceivedGoods',
query:{
SendId:item.SendId
}
})
}else if(item.Status == '待收货'){    //
this.$router.push({
path:'/docs/FHwaitGoods',
query:{
SendId:item.SendId
}
})
}
},
getStatus(val) {
if (val=='待发货') {
return "马上发货";
} else if (val =='待收货') {
return "提醒收货";
} else {
return "已完成";
}
}
}
}




