var href=window.location.href;
var hi=href.indexOf('#');
var hrefBase=hi==-1?href+"#":href.substring(0,hi+1);
var _goIndex=-1;

var _localHistory=[hi==-1?"":href.substring(hi+1)];
var _curIndex=0;

export function canGoBack(){
  return _curIndex>1;
}
export function clear(){
  _localHistory=[];
  _curIndex=-1;
}

function update(url,replace){
  var dst=_curIndex+(replace?0:1);
  if(dst<0) dst=0;
  while(_localHistory.length>dst)
    _localHistory.pop();
  _localHistory.push(url);
  _curIndex=dst;
}
export function hackHashHistory(router)
{
  var going=false;
  router.afterEach((to, from) => {
     if(_goIndex!=-1)
     {
      _localHistory[_goIndex]=to.fullPath;
      _curIndex=_goIndex;  
      _goIndex=-1;
      return;
     }
    
  });
  var orgPush=router.history.push;
  router.history.push=(location, onComplete, onAbort) =>
    orgPush.call(router.history,location,(r)=>
      {
        update(r.fullPath,false);
        // onComplete(r);
      },
      onAbort
    );
  var orgReplace=router.history.replace;
  router.history.replace=(location, onComplete, onAbort) =>
    orgReplace.call(router.history,location,(r)=>
      {
        update(r.fullPath,true);
        // onComplete(r);
      },
      onAbort
    )
  router.history.go=(n)=>{
    if(!n)return;
    var dst=_curIndex+n;
    if(dst<0 || dst>=_localHistory.length)
      return false;
    _goIndex=dst;
    window.location.replace(hrefBase+_localHistory[dst]);
  };
}



