

import Vue from 'vue'
import FastClick from 'fastclick'
import VueRouter from 'vue-router'
import AjaxPlugin from 'vux/src/plugins/ajax/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'

import vueResource from 'vue-resource'

import 'lib-flexible/flexible'
import WechatPlugin from 'vux/src/plugins/wechat/index.js'

Vue.use(WechatPlugin)







import JsBridge from '../src/script/jsbridge';

Vue.prototype.bridge = JsBridge;






import App from './App'
import router from "./router"
import {setServiceInvoker} from "./utils/KDLApi"
import {serviceInvoker} from "./utils/service-invoker"

Vue.use(AjaxPlugin)
Vue.use(VueRouter)
Vue.use(LoadingPlugin)
Vue.use(vueResource)

import IScrollView from 'vue-iscroll-view'
import IScroll from 'iscroll'
Vue.use(IScrollView, IScroll)

import base from './md/base'
Vue.use(base);

Vue.use(require('vue-wechat-title'))

setServiceInvoker(serviceInvoker);

FastClick.attach(document.body)



Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
router,
render: h => h(App)
}).$mount('#app-box')
















