
import { KDLApi } from "../../utils/KDLApi";
import XHeader from 'vux/src/components/x-header/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import { setUser, setAccessToken } from "../../utils/auth";
import { getUser } from "@/utils/auth";
import $ from "jquery";
export default {
directives: {
TransferDom
},
components: {
XHeader,
Group,
Cell,
Toast
},
data() {
return {
user: getUser(),
Type: "",
T: "http://101.132.131.224:13001/r/",
type: null,
topimg: "",
name: "",
id: "",
showPositionValue: false,
text:'',
datastatistics:''
};
},
mounted() {
window._curIndex = -1;
var that = this;
KDLApi.User.GetCurUser().then(re => {


that.name = re.data.Name;
that.id = re.data.Id;
if (re.data.Icon) {
that.topimg = re.data.Icon;
$("#userimg").css({ display: "block" });
$("#userimg1").css({ display: "none" });
} else {
$("#userimg").css({ display: "none" });
$("#userimg1").css({ display: "block" });
}

});
KDLApi.MemberMainInfo.GetMemberMainInfo().then(re => {//会员相关申请提供信息
console.log(re.data);
that.datastatistics = re.data;
},err => {

}
);
},
methods: {
Exitlogon() {
console.log("退出登录");
var that = this;
that.LoginOut();
},
myKit() {
var that = this;
var u = navigator.userAgent; 
var ua = navigator.userAgent.toLowerCase(); 

if (ua.indexOf("micromessenger") != -1) {
that.type = "Wx";
that.$router.push("/home/dev");
} else {
that.type = "Android";
that.ToMyBox();
}
},
myKitlist() {
var that = this;
var u = navigator.userAgent; 
var ua = navigator.userAgent.toLowerCase(); 

if (ua.indexOf("micromessenger") != -1) {

that.$router.push("/drugsList");
} else {

that.UseDrusList();
}
},
handleClick() {
document.getElementById("wMy").click();
},
pd() {
var that = this;
KDLApi.PairRecord.CreateNewPairRecord().then(re => {
if(re){
that.showPositionValue=true,
that.text='配对成功'
}
},err => {
alert(err.response.data.Message)

}
);
}
}
};



