
import AjaxPlugin from 'vux/src/plugins/ajax/index.js'


var _token=null;
var _handler=function serviceInvoker(svc,method,postArg,args){
var url='/api/'+svc+'/'+method;
var postData=args[postArg];
var qs=[];
for(var k in args)
if(k!=postArg)
qs.push(k+"="+encodeURIComponent(args[k]));
if(qs.length)
url+="?"+qs.join("&");
var cfg={};
if(_token)
cfg.headers={Authorization:"Bearer "+_token};
var re=postArg?
AjaxPlugin.$http.post(url,postData || {},cfg):
AjaxPlugin.$http.get(url,cfg);
return re;
};
export function filter(hd){
_handler=hd(_handler);
}
export function serviceInvoker(svc,method,postArg,args){
return _handler(svc,method,postArg,args);
}
export function setAccessToken(token){
_token=token;
}


