

import {KDLApi} from '../../utils/KDLApi';
import $ from 'jquery';
import Toast from 'vux/src/components/toast/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

export default {
components: {
Group,
XNumber,
Toast,
Alert ,
XInput
},
data () {

return {
SelectVal:1,//默认显示第一个。
product:[],
changeValue:0,
rate:"",
showPositionValue: false, //是否显示提示
position: 'center', //提示信息的位置
toastText: '', // 提示文本
type:"",
isDisble:false,
address:[],//获取当前会员地址信息
fillable:true

}
},
created(){

KDLApi.Product.GetProductInfos().then(re=>{
this.product=re.data.Items;
this.Unit=re.data.Items[0].Unit

console.log( re)



},err=>{

});


KDLApi.MemberAddress.GetMemberAddressInfo().then(re=>{
this.address=re.data





},err=>{

});

},
methods: {












Provide_Submit(){
console.log(this.SelectVal)
var reg = new RegExp("^[0-9]*$");
if(!reg.test(this.changeValue)||this.changeValue==0){
this.showPositionValue = true;
this.type="text"
this.toastText = "提供量须大于0";
return
}else if(!reg.test(this.rate)||this.rate==0){
this.showPositionValue = true;
this.type="text"
this.toastText = "请填写正确的操作费率";
return
}
if(this.address==null){

this.showPositionValue = true;
this.type="text"
this.toastText = "请添加收货信息";
setTimeout(()=>{
this.$router.push('/user/address')
},2000)
return
}

this.isDisble=true

KDLApi.StockProvide.AddStockProvide({
ProductId:this.SelectVal, //产品Id
ProvideNum:this.changeValue, //供货数量
Fee:this.rate       
}).then(re=>{
if(re){
var that=this
this.showPositionValue = true;
this.type="success"
this.toastText = "提交成功";
setTimeout(function(){
that.$router.push('/home/kcprovide')
},2000)
}else{
setTimeout(function(){
this.isDisble=false
},2000)
}


},err=>{

});



}

},


updated(){

}
}



