
import Loading from 'vux/src/components/loading/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import {KDLApi} from '../../utils/KDLApi'
import {setUser,setAccessToken} from '../../utils/auth'

function parseFragments(){
var href=window.location.href;
var i=href.indexOf('#');
if(i==-1)return {};
var t=href.indexOf('?',i+1);
var str=href.substring((t==-1?i:t)+1);
var re={};
str.split('&').forEach(v=>{
var p=v.split('=');
re[decodeURIComponent(p[0])]=decodeURIComponent(p[1]);
});
return re;
}
var fragments=parseFragments();
export default {
components: {
XInput,
XButton,
Group,
Cell
},
data () {
return {
state:fragments.state,
token:fragments.access_token,
message:fragments.message
}
},
methods: {













},
mounted(){
this.jump();
},
methods:{
jump () {
var href=window.location.href;
var i=href.indexOf('#');
if(i==-1)return {};
var t=href.indexOf('?',i+1);
var str=href.substring((t==-1?i:t)+1);
var re={};
str.split('&').forEach(v=>{
var p=v.split('=');
re[decodeURIComponent(p[0])]=decodeURIComponent(p[1]);
});
if(!re.access_token)
return;
setAccessToken(re.access_token);
KDLApi.User.GetCurUser().then(re=>{
setUser(re.data);
this.$router.replace({
path:this.$route.query.jump || "/",
query:this.$route.query.query?JSON.parse(this.$route.query.query):null
});
});
}
}
}




