
import XHeader from 'vux/src/components/x-header/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

import {KDLApi} from '../../utils/KDLApi'
import {getUser} from '@/utils/auth'
import {setUser,setAccessToken} from '../../utils/auth'
export default {
directives: {
TransferDom
},
components: {
XHeader,
Group,
Cell,
XButton,
XInput
},
data () {
return {
user:getUser(),
T:"http://101.132.131.224:13001/r/",
topimg:'',
name:'',
id:''
}
},
methods: {
save () {
this.$vux.loading.show({
text: '正在执行...'
})
console.log(this.user)
this.user.Name = this.name;
this.user.Icon = this.topimg;
KDLApi.User.Update(this.user).then(re=>{
KDLApi.User.GetCurUser().then(re=>{
this.$vux.loading.hide();
setUser(re.data);
this.user=re.data;
this.$router.back();

});
},err=>{
this.$vux.loading.hide();
alert(err.response.data.Message);
});
},
onBlur(){

},
onClick1(){
var that = this;

KDLApi.WeiXinClient.JsApiSignature(location.href.split('#')[0]).then(re=>{    //获取微信JsApi签名

that.wx(re);
});
that.$wechat.chooseImage ({
success : function(res){
var localId = res.localIds[0]; 
that.$wechat.uploadImage({
localId: localId.toString(), // 需要上传的图片的本地ID，由chooseImage接口获得
success: function (res) {
var serverId = res.serverId; 
KDLApi.WeiXinClient.SaveAndGetMediaId(serverId).then(re=>{

var imgId = re.data
that.user.Icon = imgId
that.topimg= imgId
KDLApi.User.Update(that.user).then(re=>{
KDLApi.User.GetCurUser().then(re=>{
that.$vux.loading.hide();
setUser(re.data);
that.user=re.data;

});
},err=>{
that.$vux.loading.hide();
alert(err.response.data.Message);
});

})
}
});

}
});

var upLoadType = {
Type: 0
};
that.bridge.callHandler("UploadFunc", upLoadType, function(res) {
that.topimg= res
that.user.Icon = res;
KDLApi.User.Update(that.user).then(re=>{
KDLApi.User.GetCurUser().then(re=>{
that.$vux.loading.hide();
setUser(re.data);
that.user=re.data;

});
},err=>{
that.$vux.loading.hide();
alert(err.response.data.Message);
});
})
}
},
mounted(){
window._curIndex = 2;
var that = this;
that.setTit(1,"个人信息");
that.BottomDeal(2);           
that.UrlGoBack(0,0,"我的");

KDLApi.User.GetCurUser().then(re=>{
that.topimg = re.data.Icon
that.name = re.data.Name;
that.id  = re.data.Id;
});
}
}



