import { KDLApi } from "../../utils/KDLApi";

import $ from "jquery";
export default {
  data() {
    return {
      product: []
    };
  },

  created() {
    //获取当前会员申请信息
    KDLApi.StockApply.QueryMemberStockApplyInfo({
      Paging: {
        Count: 100
      }
    }).then(
      re => {
        this.product = re.data.Items;

        console.log(this.product);
      },
      err => {}
    );
  },
  methods: {
    //根据状态显示不同的样式
    getClassName(val) {
      if (val == "未配对") {
        return "w-status";
      } else if (val == "待收货") {
        return "d-status";
      } else if (val == "处理中") {
        return "j-status";
      } else if (val == "提醒发货") {
        return "t-status";
      } else if (val == "已完成") {
        return "y-status";
      }
    }
  },

  updated() {}
};



